import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import * as echarts from 'echarts'
export function setshangPinTotalConfig(address) {
    // params
    return new Promise((resolve, reject) => {
        var option;

        get({
            url: '/getCtmUnitPrice',
            params: {
                address: address
            }
        }).then(data => {
            // x
            let xData = [],dataS=[],dataC=[]
            data.sort(function() {
                return (0.5-Math.random());
            });
            for(let info of data){
                xData.push(info.sku)
                dataC.push(info.ctmUnitPrice)
                dataS.push(info.acSales)
            }
            option = {
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'shadow',
                        label: {
                            show: true
                        }
                    }
                },
                toolbox: {
                    show: true,
                    feature: {
                        mark: { show: true },
                        dataView: { show: true, readOnly: false },
                        magicType: { show: true, type: ['line', 'bar'] },
                        restore: { show: true },
                        saveAsImage: { show: true }
                    }
                },
                calculable: true,
                legend: {
                    data: ['', ''],
                    itemGap: 5
                },
                grid: {
                    top: '12%',
                    left: '1%',
                    right: '10%',
                    containLabel: true
                },
                xAxis: [
                    {
                        type: 'category',
                        data: xData,
                        axisPointer: {
                          type: 'shadow'
                        }
                      }
                ],
                yAxis: [
                    {
                        type: 'value',
                        name: '',
                        axisLabel: {
                            formatter: function (a) {
                                a = +a;
                                return isFinite(a) ? echarts.format.addCommas(+a / 1000) : '';
                            }
                        }
                    },
                    {
                        type: 'value',
                        name: '',
                        axisLabel: {
                            formatter: '{value}'
                        }
                    }
                ],
                dataZoom: [
                    {
                        show: true,
                        start: 94,
                        end: 100
                    },
                    {
                        type: 'inside',
                        start: 94,
                        end: 100
                    },
                    {
                        show: true,
                        yAxisIndex: 0,
                        filterMode: 'empty',
                        width: 30,
                        height: '80%',
                        showDataShadow: false,
                        left: '93%'
                    }
                ],
                series: [
                    {
                        name: '',
                        type: 'bar',
                        tooltip: {
                            valueFormatter: function (value) {
                                return value + ' ';
                            }
                        },
                        data: dataS
                    },
                    {
                        name: '',
                        type: 'line',
                        yAxisIndex: 1,
                        tooltip: {
                            valueFormatter: function (value) {
                                return value + ' ';
                            }
                        },
                        data: dataC
                    }
                ]
            };
            // data
            resolve(option)
        }).catch(err => {
            resolve(option)
        })
    })
}

export default setshangPinTotalConfig