import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserRoadConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var userPath = [{
                name: 'Home',
                value: 100
            },
            {
                name: 'Page A',
                value: 80
            },
            {
                name: 'Page B',
                value: 60
            },
            {
                name: 'Page C',
                value: 40
            },
            {
                name: 'Page D',
                value: 30
            },
            {
                name: 'Page E',
                value: 20
            },
            {
                name: 'Page F',
                value: 10
            }
        ];

        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis',
                formatter: function (params) {
                    return params[0].name + ': ' + params[0].value;
                }
            },
            xAxis: {
                type: 'category',
                data: userPath.map(function (item) {
                    return item.name;
                }),
                axisLabel: {
                    rotate: 45,
                    interval: 0
                }
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                data: userPath.map(function (item) {
                    return item.value;
                }),
                type: 'bar',
                label: {
                    show: true,
                    position: 'top'
                }
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserRoadConfig