const PizZip = require("pizzip");
const Docxtemplater = require("docxtemplater");
const { saveAs } = require('file-saver')
const ImageModule = require('docxtemplater-image-module-free')
const Regex = /^data:image\/(png|jpg|svg|svg\+xml);base64,/;
var width = 200,height = 100
const imageOpts = {
    getImage(tag) {
        return base64Parser(tag);
    },
    getSize() {
        return [width,height];
    }
}



export function getBase64(charts, option = {
    maxWidth: 200,
    maxHeight: 100
}) {
    return new Promise((resolve, reject) => {
        let png = charts.getDataURL({
            pixelRatio: 25
        })
        resizeBase64(png,option.maxWidth, option.maxHeight).then(base64 => {
            resolve(base64)
        }).catch(err => {
            console.log(err)
        })
    })

}


export const render = (templateFile, options = {
    sex: "",
    address: "Doe",
    age: "0652455478",
    consumption: "New Website",
    recency: 5,
    frequency: 5,
    activeTimeCount: 2,
    activePriceCount: 1,
    view: 1,
    buy: 1,
    cart: 1,
    comment: 1,
    complain: 1,
    consult: 1,
    rfm: '',
    clv: '',
    status: '',
    userId: 1,
    RingImg: null,
    actionDescribtioon:'',
    ActionImg:null,
    userActionTable:[{
        actionName:'',
        actionDate:''
    }],
    forecast:[]
},loading) => {
    loading.setText('...')
    getFileBinaryString(templateFile).then(templateData => {
        const zip = new PizZip(templateData);
        const doc = new Docxtemplater(zip, {
            paragraphLoop: true,
            linebreaks: true,
            modules: [new ImageModule(imageOpts)]
        })
        doc.render(options)
        const out = doc.getZip().generate({
            type: 'blob',
            mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document"
        })
        loading.setText(',...')
        saveAs(out, '.docx');
    }).catch(err => {
        console.error(err)
    })
}



export function resizeBase64(base64, maxWidth, maxHeight) {
    return new Promise((resolve, reject) => {
        var image = new Image();
        //<img>
        image.src = base64;
        // 
        var tempWidth;
        var tempHeight;
        image.onload = () => {
            if (image.width > 0 && image.height > 0) {
                //   > 
                if (image.width / image.height >= maxWidth / maxHeight) {
                    if (image.width > maxWidth) {
                        tempWidth = maxWidth;
                        // 
                        tempHeight = (image.height * maxWidth) / image.width;
                    } else {
                        // 
                        tempWidth = image.width;
                        tempHeight = image.height;
                    }
                } else {//  > 
                    if (image.height > maxHeight) {
                        tempHeight = maxHeight;
                        // 
                        tempWidth = (image.width * maxHeight) / image.height;
                    } else {
                        // 
                        tempWidth = image.width;
                        tempHeight = image.height;
                    }
                }
                height = tempHeight
                width = tempWidth
                resolve(imagetoCanvas(image, tempHeight, tempWidth).toDataURL())
            } else {
                reject(null)
            }
        }
    })
}



function imagetoCanvas(image, height, width) {
    var cvs = document.createElement("canvas");
    var ctx = cvs.getContext('2d');
    cvs.width = width
    cvs.height = height;
    ctx.drawImage(image, 0, 0, cvs.width, cvs.height);
    return cvs;
}


function base64Parser(dataURL) {
    if (
        typeof dataURL !== "string" ||
        !Regex.test(dataURL)
    ) {
        return false;
    }
    const stringBase64 = dataURL.replace(Regex, "");

    const binaryString = window.atob(stringBase64);
    const len = binaryString.length;
    const bytes = new Uint8Array(len);
    for (let i = 0; i < len; i++) {
        const ascii = binaryString.charCodeAt(i);
        bytes[i] = ascii;
    }
    return bytes.buffer;
}


function getFileBinaryString(templateFile) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = (e) => {
            resolve(e.target.result);
        }
        reader.onerror = reject;
        reader.readAsBinaryString(templateFile);
    });
}

