import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserBuyConfig(address) {
    // params
    return new Promise((resolve, reject) => {

        get({
            url: "/getAllPrice",
            params: {
                address: address,
            }
        }).then(data => {
            // data

            data = data.map(e => {
                return {
                    name: e.sku,
                    value: e.totalCount,
                }

            })
            resolve({
                // 

                title: {
                    text: '',
                    left: 'center',
                    top: '10px',
                    textStyle: {
                        fontSize: 24,
                        fontWeight: 'bold'
                    }
                },
                series: [{
                    type: 'wordCloud',
                    shape: 'circle',
                    sizeRange: [20, 100],
                    rotationRange: [-90, 90],
                    rotationStep: 45,
                    gridSize: 8,
                    textStyle: {
                        fontFamily: 'Arial, sans-serif',
                        fontWeight: 'bold',
                        emphasis: {
                            shadowBlur: 10,
                            shadowColor: '#333'
                        },
                        color: function () {
                            return 'rgb(' + Math.round(Math.random() * 255) + ', ' + Math.round(Math.random() * 255) + ', ' + Math.round(Math.random() * 255) + ')';
                        }
                    },
                    data: data,
                    animation: true,
                    animationDuration: 2000,
                    animationEasing: 'quadraticInOut',
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                }],
                tooltip: {
                    show: true,
                    formatter: function (params) {
                        return '' + params.name + '<br>' + params.value;
                    }
                }


            })
        }).catch(err => {
            reject(err)
        })
    })
}

export default setuserBuyConfig