import { get } from '@/utils/axiosConfig/axios'

const actTypeStr = (type) => {
    switch (type) {
        case 1: return ''
        case 2: return ''
        case 3: return ''
        case 4: return ''
        case 5: return ''
    }
}

export function setBuyChangeConfig(id) {
    // params
    let proc = [], map = new Map(), set = new Set(), datas = [], f = true
    return new Promise((resolve, reject) => {
        proc.push(
            new Promise((resolve1, reject1) => {
                get({
                    url: "/user-act/UserTypeCountById",
                    params: {
                        userId: id
                    }
                }).then(data => {
                    // data
                    data.forEach(e => {
                        set.add(e.actDate)
                        e.actType = actTypeStr(e.actType)
                        if (map.has(e.actType)) {
                            map.get(e.actType).push([e.actDate, e.counts])
                        } else {
                            map.set(e.actType, [[e.actDate, e.counts]])
                        }
                    })
                    map.forEach((v, k) => {
                        if (v.length != 0) f = false
                        datas.push({
                            name: k,
                            type: 'line',
                            data: v.sort((e1, e2) => {
                                return new Date(e1[0]).getTime() - new Date(e2[0]).getTime()
                            }),
                            smooth: true,
                        })
                    })
                    resolve1(true)
                }).catch(err => {
                    reject1(err)
                })
            })
        )
        Promise.all(proc).then(res => {
            let xs = [...set].sort((a, b) => {
                return new Date(a).getTime() - new Date(b).getTime()
            })
            resolve({
                option:{
                    tooltip: {},
                    legend: {
                        data: [...map.keys()]
                    },
                    xAxis: {
                        type: 'category',        //
                        data: xs,
                        name: ''
                    },
                    yAxis: {
                        type: 'value',
                        name: ''        //
                    },
                    graphic: {
                        type: 'text',     // 
                        left: 'center',
                        top: 'middle',
                        silent: true,     // 
                        invisible: !f,   // 
                        style: {
                            fill: '#9d9d9d',
                            fontWeight: 'bold',
                            text: '',
                            fontFamily: 'Microsoft YaHei',
                            fontSize: '25px'
                        }
                    },
                    series: datas
                },
                xData:xs,
                data:datas
            })
        })
    })
}

export default setBuyChangeConfig