import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setrepeateBuyConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var repurchaseRateData = [{
                category: '',
                value: 80
            },
            {
                category: '',
                value: 20
            }
        ];

        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                trigger: 'item',
                formatter: '{b}: {c}%'
            },
            series: [{
                type: 'pie',
                radius: '60%',
                center: ['50%', '50%'],
                data: repurchaseRateData,
                label: {
                    formatter: '{b}: {d}%'
                },
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                },
                animationType: 'scale',
                animationEasing: 'elasticOut',
                animationDelay: function (idx) {
                    return Math.random() * 200;
                }
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setrepeateBuyConfig