import {
    get,
    post
} from '@/utils/axiosConfig/axios'

export function setGroupConfig(address, time) {
    // params
    return new Promise((resolve, reject) => {
        // 
        const data = []
        get({
            url: '/getPersonDistribution',
            params: {
                address: address,
                time: time
            }
        }).then(data => {
            data = data.map(e=>{
                return {
                    value:e.counts,
                    name:e.actType
                }
            })
            const option = {
                tooltip: {},
                legend:{
                },
                series: [{
                    type: 'pie',
                    radius: '55%',
                    top:'15%',
                    data: data,
                    emphasis: {
                        itemStyle: {
                            shadowBlur: 10,
                            shadowOffsetX: 0,
                            shadowColor: 'rgba(0, 0, 0, 0.5)',
                        },
                    },
                },]
            }
            // data
            resolve(option)
        }).catch(err => {
            reject(err)
        })
    })
}

export default setGroupConfig