import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserBuymoneyConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var data = [{
                amount: '0-100',
                count: 500
            },
            {
                amount: '100-500',
                count: 800
            },
            {
                amount: '500-1000',
                count: 300
            },
            {
                amount: '1000',
                count: 200
            }
        ];

        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                trigger: 'item',
                formatter: '{b}: {c} ({d}%)'
            },
            series: [{
                name: '',
                type: 'pie',
                radius: '50%',
                center: ['50%', '60%'],
                data: data.map(item => ({
                    name: item.amount,
                    value: item.count
                })),
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        };



        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserBuymoneyConfig