import { get, post } from '@/utils/axiosConfig/axios'

export function setGenderPiesConfig(address) {
    // params
    return new Promise((resolve, reject) => {
        get({
            url: "/getcountByAddress",
            params: {
                address: address
            }
        }).then(data => {
            // data
            resolve(
                {
                    option: {
                        tooltip: {
                            trigger: 'item',
                            formatter: '{a} <br/>{b} : {c} ({d}%)'
                        },
                        legend: {
                            orient: 'vertical',
                            left: 'left'
                        },
                        series: [
                            {
                                name: '',
                                type: 'pie',
                                radius: '50%',
                                data: [
                                    { value: data.female, name: '' },
                                    { value: data.male, name: '' },
                                ],
                                emphasis: {
                                    itemStyle: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    }
                                }
                            }
                        ]
                    },
                    data: data
                }
            )
        }).catch(err => {
            reject(err)
        })
    })
}

export default setGenderPiesConfig