import {
    get,
    post
} from '@/utils/axiosConfig/axios'

export function setLeidaConfig(address,rfmTag,time) {
    // params
    return new Promise((resolve, reject) => {
        var option;
//   // var maxr = 0,maxf=0
        // data.forEach(e=>{
        //     maxr = Math.max(e.recencyNum,maxr)
        //     maxf = Math.max(e.frequencyNum,maxf)
        // })

        // datas = data.map(e=>{
        //     return [e.frequencyNum,e.recencyNum,e.consumptionCapacityNum]
        // })
        // 
        

        get({
            url: '/getHeatMap',
            params: {
                address: address,
                rfmTag: rfmTag,
                time: time
            }
        }).then(data => {
            // data
            console.log(data)

            let mapR = new Map(),mapF = new Map()
            let datas = []
            let max = 0,min = 99999999
            let recency=new Set(),frequency=new Set()
            data.forEach(e=>{
                recency.add(e.recencyNum)
                frequency.add(e.frequencyNum)
                max = Math.max(max,e.consumptionAvg)
                min = Math.min(min,e.consumptionAvg)
            })
            console.log(datas)
            recency = [...recency]
            frequency = [...frequency]
            frequency.sort((a,b)=>a-b)
            recency.sort((a,b)=>a-b)
            frequency.forEach((e,i)=>{
                mapF.set(e,i)
            })
            recency.forEach((e,i)=>{
                mapR.set(e,i)
            })
            data.forEach(e=>{
                datas.push([mapR.get(e.recencyNum),mapF.get(e.frequencyNum),e.consumptionAvg])
            })
            option = {
                    tooltip: {
                        position: 'top',
                        formatter: function (param) {
                            var value = param.value;
                            // prettier-ignore
                            return '<div style="border-bottom: 1px solid rgba(255,255,255,.3); font-size: 18px;padding-bottom: 7px;margin-bottom: 7px">' +
                                    'FR-M'+
                                '</div>' +
                                '' + '' + recency[value[0]] + '<br>' +
                                '' + '' + value[2] + '<br>' +
                                '' + '' + frequency[value[1]] + '<br>' 
                        }
                    },
                    grid: {
                        height: '81%',
                        top: '10%'
                    },
                    xAxis: {
                        type: 'category',
                        data: recency,
                        splitArea: {
                            show: true
                        },
                        name:'()'
                    },
                    yAxis: {
                        type: 'category',
                        data: frequency,
                        splitArea: {
                            show: true
                        },
                        name:''
                    },
                    visualMap: {
                        min: min,
                        max: max,
                        calculable: true,
                        orient: 'vertical ',
                        left: '2%',
                        bottom: '55%'
                    },
                    series: [
                        {
                            name: 'M',
                            type: 'heatmap',
                            data: datas,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
            resolve(option)
        })
    })
}

export default setLeidaConfig