import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setteamBuyagainConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        const data = [{
                month: 'Jan',
                repurchaseRate: 0.2
            },
            {
                month: 'Feb',
                repurchaseRate: 0.35
            },
            {
                month: 'Mar',
                repurchaseRate: 0.45
            },
            {
                month: 'Apr',
                repurchaseRate: 0.3
            },
            {
                month: 'May',
                repurchaseRate: 0.5
            },
            {
                month: 'Jun',
                repurchaseRate: 0.4
            }
        ];

        // 
        const months = data.map(item => item.month);
        const repurchaseRates = data.map(item => item.repurchaseRate);

        // 
        const option = {
            title: {
                text: '',
                textStyle: {
                    fontWeight: 'bold',
                    fontSize: 16
                },
                left: 'center'
            },
            xAxis: {
                type: 'category',
                data: months
            },
            yAxis: {
                type: 'value',
                max: 1,
                axisLabel: {
                    formatter: '{value}%'
                }
            },
            series: [{
                type: 'line',
                data: repurchaseRates,
                smooth: true
            }]
        };
        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setteamBuyagainConfig