import {
    get,
    post
} from '@/utils/axiosConfig/axios'

export function setfrmThreeConfig(address, rfmTag, time) {
    // params
    return new Promise((resolve, reject) => {
        var datas = []
      
        get({
            url: '/getScatterPlot',
            params: {
                address: address,
                rfmTag: rfmTag,
                time: time
            }
        }).then(data => {
            // data
            console.log(data)
            var max = 0
            // data.forEach(e=>{
            //     max = Math.max(e.consumptionCapacityNum,max)
            // })
            datas = data.map(e=>{
                return [e.frequencyNum,e.consumptionCapacityNum,e.recencyNum]
            })
            var option = {
                color: ['#FFCCCC', 'GREEN'],
                tooltip: {
                    axisPointer: {
                        show: true,
                        type: 'cross',
                        lineStyle: {
                            type: 'dashed',
                            width: 1
                        }
                    },
                    formatter:({data})=>{
                        console.log(data)
                        return ':'+data[2]+''
                    }
                },
    
                toolbox: {
                    show: true,
                    x: 450,
                    feature: {
                        mark: {
                            show: true
                        },
                        dataZoom: {
                            show: true
                        },
                        dataView: {
                            show: true,
                            readOnly: false
                        },
                        restore: {
                            show: true
                        },
                        saveAsImage: {
                            show: true
                        },
                    }
                },
                xAxis: [{
                    type: 'value',
                    scale: true,
                    splitNumber: 4,
                    name:''
                }],
                yAxis: [{
                    type: 'value',
                    scale: true,
                    splitNumber: 4,
                    name:''
                }],
                series: [{
                    name: '',
                    type: 'scatter',
                    symbol: 'circle',
                    symbolSize: function (value) {
                        return value[2]/3
                    },
                    data:datas
                }]
            };
            resolve(option)
        }).catch(err => {
            reject(err)
        })
    })
}

export default setfrmThreeConfig