import "core-js/modules/es.array.push.js";
//vuex...map
import { mapState, mapMutations } from "vuex";
import { Close } from '@element-plus/icons-vue';
export default {
  data() {
    return {
      //
      visible: false,
      //
      top: 0,
      left: 0
    };
  },
  computed: {
    //vuexstatetagsthis
    ...mapState(["tags"])
  },
  watch: {
    //truecloseMenufalse
    visible(value) {
      if (value) {
        document.body.addEventListener('click', this.closeMenu);
      } else {
        document.body.removeEventListener('click', this.closeMenu);
      }
    }
  },
  methods: {
    //vuexmutationthis.xxx
    ...mapMutations(["closeTab", "cleartagsview"]),
    //
    handleClose(item, index) {
      console.log(item);
      index = this.tags.findIndex(e => {
        return e.name == item.name;
      });
      let turl = item.url;
      //
      let length = this.tags.length - 1;
      //vuex,...mapvuexvue
      this.closeTab(item);
      // 
      if (turl !== this.$route.path) {
        return;
      }
      // indexlength
      if (index === length) {
        //length=0
        if (length === 0) {
          //index
          if (this.$route.path !== "/") {
            //
            this.$router.push({
              path: "/"
            });
          }
        } else {
          //length=0.
          this.$router.push({
            path: this.tags[index - 1].url
          });
        }
      } else {
        // 
        console.log(this.tags, index);
        this.$router.push({
          path: this.tags[index].url
        });
      }
    },
    //
    tagsmenu(item) {
      //url
      if (this.$route.path !== item.url) {
        //pathurl
        this.$router.push({
          path: item.url
        });
      }
    },
    //class
    isActive(route) {
      return route === this.$route.path;
    },
    //
    openMenu(tag, e) {
      this.visible = true;
      this.selectedTag = tag;
      // const offsetLeft = this.$el.getBoundingClientRect().left
      // this.left = e.clientX - offsetLeft + 210  //
      // this.top = e.clientY + 10  //           
    },

    //
    closeMenu() {
      this.visible = false;
    },
    //vuex
    cleartags(val) {
      this.cleartagsview(val);
    },
    onHover() {}
  },
  components: {
    Close
  }
};