import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserBasicConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var userData = [{
                gender: 'Male',
                age: '18-24',
                count: 150
            },
            {
                gender: 'Male',
                age: '25-34',
                count: 230
            },
            {
                gender: 'Male',
                age: '35-44',
                count: 120
            },
            {
                gender: 'Female',
                age: '18-24',
                count: 180
            },
            {
                gender: 'Female',
                age: '25-34',
                count: 250
            },
            {
                gender: 'Female',
                age: '35-44',
                count: 140
            }
        ];

        //  x 
        var categories = {
            gender: ['Male', 'Female'],
            age: ['18-24', '25-34', '35-44']
        };

        // 
        var seriesData = [];
        for (var i = 0; i < categories.gender.length; i++) {
            for (var j = 0; j < categories.age.length; j++) {
                var gender = categories.gender[i];
                var age = categories.age[j];
                var count = 0;
                for (var k = 0; k < userData.length; k++) {
                    if (userData[k].gender === gender && userData[k].age === age) {
                        count = userData[k].count;
                        break;
                    }
                }
                seriesData.push({
                    name: gender,
                    age: age,
                    value: count
                });
            }
        }

        // 
        var option = {
            tooltip: {},
            legend: {
                data: categories.gender
            },
            xAxis: {
                data: categories.age
            },

            yAxis: {},
            series: [{
                    name: categories.gender[0],
                    type: 'bar',
                    stack: 'total',
                    emphasis: {
                        focus: 'series'
                    },
                    data: seriesData.filter(function (item) {
                        return item.name === categories.gender[0];
                    }).map(function (item) {
                        return item.value;
                    })
                },
                {
                    name: categories.gender[1],
                    type: 'bar',
                    stack: 'total',
                    emphasis: {
                        focus: 'series'
                    },
                    data: seriesData.filter(function (item) {
                        return item.name === categories.gender[1];
                    }).map(function (item) {
                        return item.value;
                    })
                }
            ]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserBasicConfig