import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setmanyValueConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        function generateRandomData(num) {
            var data = [];
            for (var i = 0; i < num; i++) {
                data.push(Math.random() * 100);
            }
            return data;
        }

        // 
        var option = {
            title: {
                text: 'OSM',
                left: 'center',
                bottom: '10%'
            },
            tooltip: {
                trigger: 'axis',
                axisPointer: {
                    type: 'shadow'
                }
            },
            legend: {
                data: ['', '', '', '']
            },
            grid: {
                top: '20%',
                left: '3%',
                right: '4%',
                bottom: '30%',
                containLabel: true
            },
            xAxis: [{
                type: 'category',
                data: ['', '', '', '', '']
            }],
            yAxis: [{
                    type: 'value',
                    name: '',
                    axisLabel: {
                        formatter: '{value} '
                    }
                },
                {
                    type: 'value',
                    name: '//',
                    axisLabel: {
                        formatter: '{value} %'
                    }
                }
            ],
            series: [{
                    name: '',
                    type: 'bar',
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                },
                {
                    name: '',
                    type: 'line',
                    yAxisIndex: 1,
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                },
                {
                    name: '',
                    type: 'line',
                    yAxisIndex: 1,
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                },
                {
                    name: '',
                    type: 'line',
                    yAxisIndex: 1,
                    data: generateRandomData(5),
                    animationDuration: 1000,
                    animationDelay: function (idx) {
                        return idx * 200;
                    }
                }
            ]
        };


        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setmanyValueConfig