import { get } from '@/utils/axiosConfig/axios'


export function setDiffChangeConfig(address, model, group) {
    // params
    let proc = [],datas = {},set = new Set()
    return new Promise((resolve, reject) => {
        proc.push(
            new Promise((resolve1, reject1) => {
                get({
                    url: "/transaction-interval/getIntervalVaries",
                    params: {
                        address: address,
                        tag: group,
                        type: model
                    }
                }).then(data => {
                    // data
                    datas.change = data.map(e=>{
                        set.add(e.startDate)
                        return [e.startDate,e.average]
                    })
                    resolve1(datas)
                }).catch(err => {
                    reject1(err)
                })
            })
        )
        Promise.all(proc).then(res=>{
            resolve( {
                tooltip:{},
                legend:{
                    data:[...set]
                },
                xAxis:{
                    type:'category',        //
                    data:[...set],
                    name:''
                },
                yAxis:{
                    type:'value',
                    name:''        //
                },
                graphic: {
                    type: 'text',     // 
                    left: 'center',
                    top: 'middle',
                    silent: true,     // 
                    invisible: datas.change.length > 0,   // 
                    style: {
                        fill: '#9d9d9d',
                        fontWeight: 'bold',
                        text: '',
                        fontFamily: 'Microsoft YaHei',
                        fontSize: '25px'
                    }
                },
                series:[{
                    name:'',
                    type:'line',
                    data:datas.change,
                    smooth:true,        //      
                }]
            })
        })
    })
}

export default setDiffChangeConfig