import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'
import * as echarts from 'echarts'

export function setretentionConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var retentionRateData = [{
                month: '1',
                rate: 0.7
            },
            {
                month: '2',
                rate: 0.6
            },
            {
                month: '3',
                rate: 0.5
            },
            {
                month: '4',
                rate: 0.4
            },
            {
                month: '5',
                rate: 0.3
            }
        ];

        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis',
                formatter: function (params) {
                    var data = params[0].data;
                    return data.month + '<br/>' + (data.rate * 100) + '%';
                }
            },
            xAxis: {
                type: 'category',
                data: retentionRateData.map(function (item) {
                    return item.month;
                })
            },
            yAxis: {
                type: 'value',
                axisLabel: {
                    formatter: '{value}%'
                }
            },
            series: [{
                type: 'line',
                smooth: true,
                lineStyle: {
                    width: 3,
                    shadowBlur: 10,
                    shadowColor: 'rgba(0, 0, 0, 0.3)'
                },
                areaStyle: {
                    color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                        offset: 0,
                        color: 'rgba(64, 141, 255, 0.8)'
                    }, {
                        offset: 1,
                        color: 'rgba(64, 141, 255, 0)'
                    }])
                },
                itemStyle: {
                    color: '#408DFF'
                },
                data: retentionRateData.map(function (item) {
                    return item.rate * 100;
                }),
                label: {
                    show: true,
                    position: 'top',
                    formatter: '{c}%'
                }
            }]
        };



        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setretentionConfig