import { get} from '@/utils/axiosConfig/axios'


export function setAgeRingConfig(address) {
    // params
    return new Promise((resolve, reject) => {
        get({
            url: "/getcountbycondition",
            params: {
                address: address
            }
        }).then(data => {
            // data
            resolve(
                {
                    tooltip: {
                        trigger: 'item',
                        formatter: '{a} <br/>{b} : {c} ({d}%)'
                    },
                    legend: {
                        top: '',
                        left: '-5'
                    },
                    series: [
                        {
                            name: '',
                            type: 'pie',
                            radius: ['40%', '70%'],
                            avoidLabelOverlap: false,
                            label: {
                                show: false,
                                position: 'center'
                            },
                            emphasis: {
                                label: {
                                    show: true,
                                    fontSize: 10,
                                    fontWeight: 'bold'
                                }
                            },
                            labelLine: {
                                show: false
                            },
                            data: [
                                { value: data['less10'], name: '10' },
                                { value: data['less15'], name: '1015' },
                                { value: data['less20'], name: '1525' }
                            ]
                        }
                    ]
                }
            )
        }).catch(err=>{
            reject(err)
        })
    })
}

export default setAgeRingConfig