import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setteamreserveConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var data = [{
                user: 'A',
                retentionRate: 0.6,
                conversionRate: 0.2
            },
            {
                user: 'B',
                retentionRate: 0.4,
                conversionRate: 0.3
            },
            {
                user: 'C',
                retentionRate: 0.8,
                conversionRate: 0.4
            },
            {
                user: 'D',
                retentionRate: 0.5,
                conversionRate: 0.1
            },
            {
                user: 'E',
                retentionRate: 0.7,
                conversionRate: 0.3
            }
        ];

        // 
        var userData = data.map(item => item.user);
        var retentionRateData = data.map(item => item.retentionRate);
        var conversionRateData = data.map(item => item.conversionRate);

        // 
        var option = {
            title: {
                text: '',
                left: 'center',
                top: '90%'
            },
            tooltip: {},
            legend: {
                data: ['', '']
            },
            xAxis: {
                type: 'category',
                data: userData
            },
            yAxis: [{
                    type: 'value',
                    name: '',
                    axisLabel: {
                        formatter: '{value}%'
                    }
                },
                {
                    type: 'value',
                    name: '',
                    axisLabel: {
                        formatter: '{value}%'
                    }
                }
            ],
            series: [{
                    name: '',
                    type: 'bar',
                    data: retentionRateData.map(rate => rate * 100) // 
                },
                {
                    name: '',
                    type: 'line',
                    yAxisIndex: 1,
                    data: conversionRateData.map(rate => rate * 100) // 
                }
            ]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setteamreserveConfig