import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserInterestConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var userInfluence = [{
                name: 'User A',
                fansCount: 10000,
                interactionLevel: 1800
            },
            {
                name: 'User B',
                fansCount: 5000,
                interactionLevel: 1600
            },
            {
                name: 'User C',
                fansCount: 3000,
                interactionLevel: 1400
            },
            {
                name: 'User D',
                fansCount: 8000,
                interactionLevel: 1700
            },
            {
                name: 'User E',
                fansCount: 2000,
                interactionLevel: 1500
            }
        ];

        // 
        var option = {
            tooltip: {
                formatter: function (params) {
                    return params.name + '<br>Fans Count: ' + params.value[0] + '<br>Interaction Level: ' + params.value[1];
                }
            },
            grid: {
                left: 'center', // 
                top: 'middle', // 
                width: '60%', // 
                height: '60%' // 
            },
            xAxis: {
                type: 'value',
                name: 'Fans Count',
                splitLine: {
                    show: false
                }
            },
            yAxis: {
                type: 'value',
                name: 'Interaction Level',
                splitLine: {
                    show: false
                }
            },
            series: [{
                type: 'scatter',
                symbolSize: function (data) {
                    return Math.sqrt(data[0]) / 2;
                },
                data: userInfluence.map(function (user) {
                    return [user.fansCount, user.interactionLevel];
                }),
                label: {
                    emphasis: {
                        show: true,
                        formatter: function (params) {
                            return params.data[2];
                        },
                        position: 'top'
                    }
                },
                itemStyle: {
                    color: function () {
                        return 'rgb(' + [
                            Math.round(Math.random() * 160),
                            Math.round(Math.random() * 160),
                            Math.round(Math.random() * 160)
                        ].join(',') + ')';
                    }
                }
            }]
        };
        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserInterestConfig