import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserCostConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var userPreferences = [{
                name: 'Category A',
                value: 30
            },
            {
                name: 'Category B',
                value: 50
            },
            {
                name: 'Category C',
                value: 40
            },
            {
                name: 'Category D',
                value: 20
            },
            {
                name: 'Category E',
                value: 60
            }
        ];

        // 
        var option = {

            tooltip: {},
            xAxis: {
                type: 'category',
                data: userPreferences.map(function (preference) {
                    return preference.name;
                })
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                type: 'bar',
                data: userPreferences.map(function (preference) {
                    return preference.value;
                }),
                itemStyle: {
                    color: function () {
                        return 'rgb(' + [
                            Math.round(Math.random() * 160),
                            Math.round(Math.random() * 160),
                            Math.round(Math.random() * 160)
                        ].join(',') + ')';
                    }
                }
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserCostConfig