import { get } from '@/utils/axiosConfig/axios'


export function setTimePeriedConfig(actType, user) {
    // params
    let proc = [], datas = {},set = new Set()
    return new Promise((resolve, reject) => {
        proc.push(
            new Promise((resolve1, reject1) => {
                get({
                    url: "/user-act/skuByCounts",
                    params: {
                        actType: actType,
                        user: user
                    }
                }).then(data => {
                    // data
                    datas.timePeried = []
                    for(let info of data){
                        datas.timePeried.push(info.counts)
                        set.add(info.sku)
                    }
                    resolve1(datas)
                }).catch(err => {
                    reject1(err)
                })
            })
        )
        Promise.all(proc).then(res => {
            resolve({
                tooltip: {},
                legend: {
                    data: [...set]
                },
                xAxis: {
                    name:'',
                    show:true,
                    type: "value",
                },
                yAxis: {
                    type: "category",
                    data: [...set],
                    name:''
                },
                graphic: {
                    type: 'text',     // 
                    left: 'center',
                    top: 'middle',
                    silent: true,     // 
                    invisible: datas.timePeried.length > 0,   // 
                    style: {
                        fill: '#9d9d9d',
                        fontWeight: 'bold',
                        text: '',
                        fontFamily: 'Microsoft YaHei',
                        fontSize: '25px'
                    }
                },
                series: [{
                    name: '',
                    type: 'bar',
                    data: datas.timePeried
                }]
            })
        })
    })
}

export default setTimePeriedConfig