import {
    get,
    post
} from '@/utils/axiosConfig/axios'

export function setcityRate(address,sku) {
    // params
    return new Promise((resolve, reject) => {
        get({
            url: "/getAllCounts",
            params: {
                address: address,
                sku:sku
            }
        }).then(data => {
            // data
            let xData = [], addressData = []
            addressData = data.map((e, i) => {
                xData.push(e[address==null?'province':'city'])
                return {
                    name: e[address==null?'province':'city'],
                    value: e.addressNumCount
                }
            })
            resolve({
                option: {
                    legend: {
                        type: 'scroll',
                        orient: 'vertical',
                        right: 10,
                        top: 20,
                        bottom: 20,
                        data: xData
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            mark: {
                                show: true
                            },
                            dataView: {
                                show: true,
                                readOnly: false
                            },
                            restore: {
                                show: true
                            },
                            saveAsImage: {
                                show: true
                            }
                        }
                    },
                    series: [{
                        name: '',
                        type: 'pie',
                        radius: [50, 120],
                        center: ['50%', '50%'],
                        itemStyle: {
                            borderRadius: 8
                        },
                        data: addressData
                    }]


                },
                xData: xData,
                data: data,
                addressData: addressData
            })
        }).catch(err => {
            reject(err)
        })
    })
}

export default setcityRate