import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function settimeConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var data = [{
                interval: '0-7',
                count: 500,
                average: 4.5
            },
            {
                interval: '7-30',
                count: 800,
                average: 15.2
            },
            {
                interval: '30-90',
                count: 300,
                average: 45.6
            },
            {
                interval: '90',
                count: 200,
                average: 120.3
            }
        ];

        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                formatter: function (params) {
                    return params.name + '<br/>: ' + params.data.count + '<br/>: ' + params.data.average + '';
                }
            },
            xAxis: {
                type: 'category',
                data: data.map(item => item.interval)
            },
            yAxis: {
                type: 'value',
                name: ''
            },
            series: [{
                name: '',
                type: 'scatter',
                data: data.map(item => [item.interval, item.average]),
                symbolSize: function (data) {
                    return Math.sqrt(data[1]) * 10;
                },
                itemStyle: {
                    color: '#5AB1EF'
                },
                emphasis: {
                    itemStyle: {
                        borderColor: '#5AB1EF',
                        borderWidth: 2
                    },
                    label: {
                        show: true,
                        formatter: function (params) {
                            return params.data[0];
                        },
                        position: 'top'
                    }
                }
            }]
        };




        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default settimeConfig