import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserhobbyConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var data = [
            ['', '', 500],
            ['', '', 800],
            ['', '', 300],
            ['', '', 200],
            ['', '', 600],
            ['', '', 400],
            ['', '', 350],
            ['', '', 250],
            ['', '', 700],
            ['', '', 450]
        ];

        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                position: 'top'
            },
            grid: {
                height: '50%',
                top: '20%',
                left: '60px',
            },
            xAxis: {
                type: 'category',
                data: data.map(item => item[0]),
                splitArea: {
                    show: true
                }
            },
            yAxis: {
                type: 'category',
                data: data.map(item => item[1]),
                splitArea: {
                    show: true
                }
            },
            visualMap: {
                min: 0,
                max: 1000,
                calculable: true,
                orient: 'horizontal',
                left: 'center',
                bottom: '2%'
            },
            series: [{
                name: '',
                type: 'heatmap',
                data: data.map(item => [item[0], item[1], item[2]]),
                label: {
                    show: true
                },
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        };




        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserhobbyConfig