import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setBackMoneyConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var refundRateData = [{
                channel: 'A',
                refundRate: 0.05
            },
            {
                channel: 'B',
                refundRate: 0.08
            },
            {
                channel: 'C',
                refundRate: 0.03
            },
            {
                channel: 'D',
                refundRate: 0.06
            },
            {
                channel: 'E',
                refundRate: 0.04
            }
        ];
        var option = {
            title: {
                text: ''
            },
            xAxis: {
                type: 'category',
                data: refundRateData.map(function (item) {
                    return item.channel;
                })
            },
            yAxis: {
                type: 'value',
                axisLabel: {
                    formatter: '{value}%'
                }
            },
            series: [{
                type: 'bar',
                data: refundRateData.map(function (item) {
                    return item.refundRate * 100;
                })
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setBackMoneyConfig