import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'
import * as echarts from 'echarts'

export function setclvLostConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        // 
        var data = [{
                interval: 5,
                percentage: 0.65
            },
            {
                interval: 10,
                percentage: 0.85
            },
            {
                interval: 15,
                percentage: 0.95
            },
            {
                interval: 20,
                percentage: 0.92
            },
            {
                interval: 25,
                percentage: 0.88
            },
            {
                interval: 30,
                percentage: 0.76
            },
            // ...
        ];

        // 
        var lossThreshold = 0;
        for (var i = 0; i < data.length; i++) {
            if (data[i].percentage >= 0.95) {
                lossThreshold = data[i].interval;
                break;
            }
        }



        // 
        var option = {
            title: {
                text: '',
                subtext: '' + lossThreshold + '',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis',
                formatter: '{b}{c}%'
            },
            xAxis: {
                type: 'category',
                data: data.map(function (item) {
                    return item.interval + '';
                }),
                name: ''
            },
            yAxis: {
                type: 'value',
                name: '%'
            },
            series: [{
                type: 'bar',
                name: '',
                data: data.map(function (item) {
                    return {
                        value: item.percentage * 100,
                        interval: item.interval
                    };
                }),
                label: {
                    show: true,
                    formatter: '{c}%'
                }
            }, {
                type: 'line',
                name: '',
                data: [lossThreshold],
                symbol: 'none',
                lineStyle: {
                    type: 'dashed'
                },
                label: {
                    show: true,
                    formatter: ''
                }
            }]
        };

        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setclvLostConfig