import {
    get,
    post
} from '@/utils/axiosConfig/axios'

export function setAreaConfig(address,province, city, level = 1) {
    console.error(address,province,city,level)
    // params
    return new Promise((resolve, reject) => {
        get({
            url: '/ProCitCount',
            params: {
                province: province,
                city: city
            }
        }).then(data => {
            let info = (address == null ? 'province' : (level == 1 ? 'city' : 'county'))
             
            var xAxis = data.map((e) => {
                return e[info]
            })
            var series = data.map((e) => { return e.counts })

             
            resolve({
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        crossStyle: {
                            color: '#999'
                        }
                    }
                },
                toolbox: {
                    feature: {
                        dataView: {
                            show: true,
                            readOnly: false
                        },
                        magicType: {
                            show: true,
                            type: ['line', 'bar']
                        },
                        restore: {
                            show: true
                        },
                        saveAsImage: {
                            show: true
                        }
                    }
                },
                legend: {
                    data: xAxis
                },
                xAxis: [{
                    type: 'category',
                    axisLabel: {
                        interval:0,//x,
                        rotate:xAxis.length>10?40:0, //45

                    },
                    data:xAxis,
                    axisPointer: {
                        type: 'shadow'
                    }
                }],
                yAxis: [{
                    type: 'value',
                    name: '',
                    axisLabel: {
                        formatter: (value)=>{
                            if (value >= 10000 && value < 10000000) {
                                value = value / 10000 + "";
                            } else if (value >= 10000000) {
                                value = value / 10000000 + "";
                            }
                            return value; 
                        }
                    }
                },
                {
                    type: 'value',
                    name: '',
                    show:false,
                    axisLabel: {
                        formatter: '{value}'
                    }
                }
                ],
                series: [{
                    name: '',
                    type: 'bar',
                    tooltip: {
                        valueFormatter: function (value) {
                            return value + ' ';
                        }
                    },
                    data: series
                },
                {
                    name: '',
                    type: 'line',
                    yAxisIndex: 1,
                    tooltip: {
                        show:false,
                    },
                    data: series
                }
                ]

            })

        }).catch(err => {
            reject(err)
        })
    })
}

export default setAreaConfig