import {
    get,
    post
} from '@/utils/axiosConfig/axios'
import {
    CENTERED_ALIGNMENT
} from 'element-plus/es/components/virtual-list/src/defaults'

export function setuserActionConfig(params1, parmas2) {
    // params
    return new Promise((resolve, reject) => {
        var channelData = [{
                channel: '',
                purchaseCount: 100,
                purchaseAmount: 5000
            },
            {
                channel: '',
                purchaseCount: 80,
                purchaseAmount: 4000
            },
            {
                channel: '',
                purchaseCount: 50,
                purchaseAmount: 3000
            },
            // ...
        ];

        // 
        var option = {
            title: {
                text: '',
                left: 'center'
            },
            tooltip: {
                trigger: 'axis'
            },
            xAxis: {
                type: 'category',
                data: channelData.map(item => item.channel)
            },
            yAxis: [{
                    type: 'value',
                    name: ''
                },
                {
                    type: 'value',
                    name: ''
                }
            ],
            series: [{
                    name: '',
                    type: 'bar',
                    data: channelData.map(item => item.purchaseCount)
                },
                {
                    name: '',
                    type: 'bar',
                    yAxisIndex: 1,
                    data: channelData.map(item => item.purchaseAmount)
                }
            ]
        };


        // get({
        //     url: url,
        //     params: {
        //         params1: params1,
        //         parmas2: parmas2
        //     }
        // }).then(data => {
        //     // data
        resolve(option)
        // }).catch(err=>{
        //     reject(err)
        // })
    })
}

export default setuserActionConfig