import { get } from '@/utils/axiosConfig/axios'


export function setTimePeriedConfig(address, model, group) {
    // params
    let proc = [], datas = {}, set = new Set(),map = {
        today:'(2018-4-15)',
        yesterday:'',
        inweek:'',
        inmonth:'',
        outmonth:''
    }
    return new Promise((resolve, reject) => {
        proc.push(
            new Promise((resolve1, reject1) => {
                get({
                    url: "/transaction-interval/timePeriod",
                    params: {
                        address: address,
                        tag: group,
                        type: model
                    }
                }).then(data => {
                    // data
                    datas.timePeried = []
                    for(let info in data){
                        datas.timePeried.push(data[info])
                        set.add(map[info])
                    }
                    resolve1(datas)
                }).catch(err => {
                    reject1(err)
                })
            })
        )
        Promise.all(proc).then(res => {
            resolve({
                tooltip: {},
                legend: {
                    data: [...set]
                },
                xAxis: {
                    name:'',
                    show:true,
                    type: "value",
                },
                yAxis: {
                    type: "category",
                    data: [...set],
                    name:''
                },
                graphic: {
                    type: 'text',     // 
                    left: 'center',
                    top: 'middle',
                    silent: true,     // 
                    invisible: datas.timePeried.length > 0,   // 
                    style: {
                        fill: '#9d9d9d',
                        fontWeight: 'bold',
                        text: '',
                        fontFamily: 'Microsoft YaHei',
                        fontSize: '25px'
                    }
                },
                series: [{
                    name: '',
                    type: 'bar',
                    data: datas.timePeried
                }]
            })
        })
    })
}

export default setTimePeriedConfig